<template>
  <div class="login-page">
    <el-card class="login-form">
      <img src="@/assets/logo.png" alt="" class="logo" />
      <el-form ref="form" :model="form" :rules="rules">
        <el-form-item prop="loginName">
          <el-input
            v-model="form.loginName"
            placeholder="请输入账号"
            prefix-icon="el-icon-mobile"
          ></el-input>
        </el-form-item>
        <el-form-item prop="password">
          <el-input
            v-model="form.password"
            :type="eyeShow ? 'password' : 'text'"
            placeholder="请输入密码"
            prefix-icon="el-icon-lock"
          >
            <i
              slot="suffix"
              class="iconfont icon-yanjing eyes"
              v-if="eyeShow"
              @click="eyeShow = !eyeShow"
            ></i>
            <i
              slot="suffix"
              class="iconfont icon-JC_039 eyes"
              v-else
              @click="eyeShow = !eyeShow"
            ></i>
          </el-input>
        </el-form-item>
        <el-form-item>
          <el-row>
            <el-col :span="17">
              <el-input
                v-model="form.code"
                placeholder="请输入验证码"
                prefix-icon="el-icon-edit-outline"
              ></el-input>
            </el-col>
            <el-col :span="7">
              <img :src="blob" alt="" @click="getImgCode()" v-imgError="defaultImg" class="validate"/>
            </el-col>
          </el-row>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="login()">登录</el-button>
        </el-form-item>
      </el-form>
    </el-card>
  </div>
</template>

<script>
import { imgCode } from '@/api/login'
export default {
  data () {
    return {
      form: {
        loginName: 'admin',
        password: 'admin',
        code: '',
        mobile: '',
        clientToken: '',
        loginType: 0,
        account: ''
      },
      eyeShow: true,
      blob: '',
      rules: {
        loginName: [
          { required: true, message: '请输入用户名', trigger: 'blur' }
        ],
        password: [
          { required: true, message: '请输入密码', trigger: 'blur' }
        ]
      },
      defaultImg: require('@/assets/imgerror.png')
    }
  },
  async created () {
    this.getImgCode()
    console.log(1)
  },
  methods: {
    // 生成图片请求随机字符串
    getRandom (len) {
      len = len || 32
      const chars = 'ABCDEFGHJKMNPQRSTWXYZabcdefhijkmnprstwxyz2345678'
      let str = ''
      for (let i = 0; i < len; i++) {
        // 将随机数指定的索引对应的字符拼接起来
        str += chars.charAt(Math.floor(Math.random() * chars.length))
      }
      return str
    },
    // 获取图片验证码
    async getImgCode () {
      const imgC = this.getRandom(16)
      this.form.clientToken = imgC
      const blob = await imgCode(imgC)
      console.log(blob)
      this.blob = window.URL.createObjectURL(blob)
    },
    async login () {
      // 先校验
      await this.$refs.form.validate()
      // 触发user模块中的登录方法
      this.$store.dispatch('user/loginAction', this.form)
    }
  }
}
</script>

